*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.flexBox{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 50px auto;
  
}
.my-Navbar{
  width: 100%;
  background-color: rgb(57, 53, 53);
  display: flex;
  flex-direction: row;
  color: white;

}
.my-Navbar ul li {
  margin: 0px 10px;
  list-style: none;
  display: inline;
}
.my-Navbar ul li a {
  text-decoration: none;
}

